<template>
  <div class="swiper-box">
    <!-- {{list}} -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          v-for="(item,index) in list"
          :key="index"
          class="swiper-slide"
          :style="`background-image:url(${item.img})`"
        ></div>
        <!-- <div
        class="swiper-slide"
         style="background:red"
      ></div>
      <div
        class="swiper-slide"
        style="background:pink"
      ></div>
      <div
        class="swiper-slide"
         style="background:orange"
      ></div>
      <div
        class="swiper-slide"
        style="background:green"
        ></div>-->
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import mySwiper from "swiper";
export default {
  created() {
    if (this.list.length) return;
    // 若vuex中的 bannerList有数据，说明已经请求过了 不用再去请求了
    this.$store.dispatch("getBanner");
  },
  mounted() {
    //   dom 加载完成之后触发
   this.init();
  },
  // 组件中使用vuex数据，基本都是使用computed
  computed: {
    list() {
      return this.$store.state.bannerList;
    }
  },
  methods: {
    // 初始化轮播图
    init() {
      new mySwiper(".swiper-container", {
        loop: true, // 循环模式选项
        effect: "cube",
        grabCursor: true,
        cubeEffect: {
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94
        },
        pagination: {
          el: ".swiper-pagination"
        }
      });
    }
  },
  updated() {
    // 可以new的前提是 页面上已经有了各个轮播图
    // 只要数据更新就会触发这个函数
    console.log(this.list);
    // 在mounted执行时，this.list还是一个空数组
    // 这是页面上，没有任何swiper-slide 此时初始化swiper没有我们想要的效果
    this.init();
  }
};
</script>

<style lang="less" scoped>
.swiper-box {
  background-color: #ccc;
  position: relative;
  height: 4rem;
  top: 1rem;
  overflow: hidden;
}
.swiper-container {
  width: 4rem;
  height: 4rem;
  position: absolute;
  left: 25%;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
}
</style>
